@import "../../global.scss";

.portfolio {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        margin-top: 30px;
        margin-bottom: 50px;
        font-size: 50px;
        
      @include mobile{
        font-size: 30px;
      }
    }

    ul{
        margin: 30px;
        padding: 0;
        list-style: none;
        display: flex;
      
        @include mobile{
          margin: 10px 0;
          flex-wrap: wrap;
          justify-content: center;
        }
      
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        column-gap: 20px;

        @include mobile{
          width: 100%;
        }

        .item {
            width: 300px;
            height: 220px;
            border-radius: 20px;
            border: 1px solid rgb(240, 239, 239);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
              width: 280px;
              height: 200px;
            }

            h3 {
                position: absolute;
                font-size: 20px;
              }
        
              img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
              }

              &:hover {
                background-color: $mainColor;
                img {
                  opacity: 0.2;
                  z-index: 0;
                }
            }
        }
    }

   
}